@import url(https://fonts.googleapis.com/css?family=Lobster&subset=latin-ext);
@import url(https://fonts.googleapis.com/css?family=Comfortaa&subset=latin-ext);
@import url(https://fonts.googleapis.com/css?family=Cutive+Mono);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/*
 * TODO: use sass (https://medium.com/@Connorelsea/using-sass-with-create-react-app-7125d6913760)
 */

html, body, * {
  font: 14px 'Comfortaa', cursive;
}

html, body, #root {
  padding: 0px;
  margin: 0px;
  width: 100%;
  height: 100%;
}

#root {
  background: #888;
}

#App {
  height: 100%;
}

table {
  border-collapse: collapse;
  height: 100%;
  width: 100%;
}

tr {
  border-collapse: collapse;
}

td {
  text-align: center;
  min-width: 75px;
  border: 1px solid black;
  border-collapse: collapse;
  padding: 0px 15px;
  font-size: 1.3em;
}

.Header {
  background: none;
  color: black;
  border: none;
  font-size: 1.5em;
  text-shadow: 0px 0px 10px rgba(0, 0, 0, 0.25);
}

#ToolBar td {
  background: none;
  border: none;
  text-align: center;
}

#ToolBar td button {
  margin: 1em 1.5em 1.5em 1.5em;
}

button {
  margin: 5px;
  min-height: 50px;
  border-radius: 10px;
  background: #ccc;
  color: black;
  border: 1px solid #444;
  text-shadow: 0px 0px 10px rgba(0, 0, 0, 0.3);
}

.ro {
  background: #aaa;
  color: black;
  text-shadow: 0px 0px 10px rgba(0, 0, 0, 0.25);
}

.Header.ro {
  background: none;
  color: #444;
  text-shadow: 0px 0px 5px rgba(64, 64, 64, 0.5);
}

tr.sep {
  border: none;
  height: 1em;
}

._name {
  border: none;
  color: black;
  font-size: 1.9em;
  text-transform: capitalize;
  font-family: 'Lobster', cursive;
  text-shadow: 0px 0px 5px rgba(0, 0, 0, 0.1);
  padding: 15px;
  padding-bottom: 0px;
}

._1, ._2, ._3, ._4, ._5, ._6, ._total, ._bonus, ._diff, ._TOTAL, ._mini, ._MAXI, ._chance, ._brelan, ._suite, ._full, ._carre, ._yams {
  color: black;
  font-size: 1.5em;
  text-shadow: 0px 0px 10px rgba(0, 0, 0, 0.15);
  font-family: 'Cutive Mono', monospace;
  font-weight: bold;
}

._1:nth-child(odd), ._2:nth-child(odd), ._3:nth-child(odd),
._4:nth-child(odd), ._5:nth-child(odd), ._6:nth-child(odd) {
  background: #e8b808;
}

._1:nth-child(even), ._2:nth-child(even), ._3:nth-child(even),
._4:nth-child(even), ._5:nth-child(even), ._6:nth-child(even) {
  background: #d0a000;
}

._total:nth-child(odd), ._bonus:nth-child(odd), ._diff:nth-child(odd), ._TOTAL:nth-child(odd) {
  background: #989898;
}

._total:nth-child(even), ._bonus:nth-child(even), ._diff:nth-child(even), ._TOTAL:nth-child(even) {
  background: #808080;
}

._mini:nth-child(odd), ._MAXI:nth-child(odd) {
  background: #e888e8;
}

._mini:nth-child(even), ._MAXI:nth-child(even) {
  background: #d070d0;
}

._chance:nth-child(odd) {
  background: #28e888;
}

._chance:nth-child(even) {
  background: #10d070;
}

._brelan:nth-child(odd), ._suite:nth-child(odd), ._full:nth-child(odd),
._carre:nth-child(odd), ._yams:nth-child(odd) {
  background: #98b8e8;
}

._brelan:nth-child(even), ._suite:nth-child(even), ._full:nth-child(even),
._carre:nth-child(even), ._yams:nth-child(even) {
  background: #80a0d0;
}

