.Gluton {
  width: 100%; /* Swallow the rest of the line */
  border: none;
  background: none;
  visibility: hidden;
}

.modal {
  position: fixed;
  top: 0px;
  bottom: 0px;
  color: white;
  left: 0px;
  right: 0px;
  background-color: rgba(0,0,0,0.8);
  padding: 50px;
}

.modal .content {
  margin: auto auto;
  padding: 30px;
  background: #444;
  border-radius: 25px;
}

.modal .content label {
  padding: 30px auto 15px auto;
}

input#new_player_name,
input#rename_player_name {
  display: inline-block;
  width: 360px;
  height: 2em;
  margin: 15px auto 30px auto;
}

button#new_player_submit,
button#delete_player_submit,
button#rename_player_submit {
  width: 360px;
}

button#new_player_submit {
  margin: 30px auto;
}

button#delete_player_submit {
  color: white;
  background: red;
  margin: 30px auto;
}

.line {
  background-image: url(http://gembrot.bagus.org/annule.png) !important;
  background-size: 100% 100% !important;
}

button.immed {
  width: 22%;
}

